.orthofloat-stripes {
    display: block;
    position: absolute;
    height: 100%;
    max-height: 100vh;
    min-width: calc(100% + 100px);
    overflow: hidden;

    .orthofloat-stripe {
        display: inline-block;
        height: 100%;
    }
}


